.activities-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .activity-card {
    width: 300px;
    margin: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  .activity-card img {
    width: 100%;
    height: 250px;
    border-radius: 5px;
    margin-bottom: 10px;
  }
  
  .activity-details h2 {
    margin-bottom: 10px;
  }
  
  .activity-details p {
    margin-bottom: 5px;
  }
  
  .activity-details p:last-child {
    margin-bottom: 0;
  }
  