.contacts-container {
    display: flex;
    /* margin-top: 27px; Add margin to avoid overlapping with the fixed topnav */
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
  }
  
  .contact-form {
    width: 80%;
    margin: 20px;
    padding: 20px;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  .contact-form h2 {
    margin-bottom: 20px;
  }
  
  form {
    display: flex;
    flex-direction: column;
  }
  
  input,
  textarea {
    margin-bottom: 20px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  button {
    padding: 10px 20px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  button:hover {
    background-color: #0056b3;
  }
  
  .social-media-newsletter {
    width: 80%;
    display: flex;
    justify-content: space-between;
  }
  
  .social-media,
  .newsletter {
    width: 48%;
    padding: 20px;
    border-radius: 10px;
    background-color: #f9f9f9;
  }
  
  .social-media h2,
  .newsletter h2 {
    margin-bottom: 20px;
  }
  
  .social-icons {
    display: flex;
    align-items: center;
  }
  
  .social-icons a {
    font-size: 24px;
    margin-right: 10px;
    color: #333;
    transition: color 0.3s ease;
  }
  
  .social-icons a:hover {
    color: #007bff;
  }
  
  .newsletter form {
    display: flex;
  }
  
  .newsletter input {
    flex: 1;
    margin-right: 10px;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }
  
  .newsletter button {
    padding: 10px 20px;
    background-color: #28a745;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .newsletter button:hover {
    background-color: #218838;
  }
  