.App {
  font-family: Arial, sans-serif;
  text-align: center;
  height: 100vh;
  display: flex;
  flex-direction: column;
}

.home {
  display: flex;
  flex-direction: column;
  height: 100%;
  overflow-y: auto;
}

/* Hide the scrollbar */
.home::-webkit-scrollbar {
  width: 0;
  background: transparent; /* For browsers supporting this property */
}

/* For Firefox */
.home {
  scrollbar-width: none; /* For Firefox */
}

/* Styles for the top section */
.top-section {
  height: 40vh;
  position: relative;
  background: rgba(100, 211, 60, 0.5); /* Light green background with transparency */
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  flex-shrink: 0;
  overflow: hidden; /* Prevent scrollbars from appearing */
}

/* Center image styling */
.center-image {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 110%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.center-image img {
  width: auto;
  height: 100%;
  opacity: 0.8;
   /* Make the image semi-transparent */
}

/* Styling for the small images */
.small-images {
  position: relative;
  display: flex;
  justify-content: center;
  z-index: 1; /* Ensure small images are in front of the center image */
  margin-top: 150px;
}

.small-images img {
  width: 10%;
  height: auto;
  margin: 0 5px;
}


.small-images img {
  width: 10%;
  height: auto;
  margin: 0 5px;
}

.scrollable-content {
  flex-grow: 1;
  overflow-y: auto;
  padding: 20px;
  background-color: #f4f4f4;
}

.scrollable-content section {
  max-width:900px;
  margin: 0 auto;
  text-align: left;
}

.footer {
  background: rgba(100, 211, 60, 0.5); /* Light green background with transparency */
  color: black;
  padding: 20px;
  text-align: center;
  position: relative;
  flex-shrink: 0;
  overflow-y: auto;
}


.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}














