/* Button styles */
.button {
  display: inline-block;
  padding: 10px 20px;
  background-color: #007bff;
  color: #fff;
  text-decoration: none;
  border-radius: 5px;
}

.button:hover {
  background-color: #0056b3;
}






.container {
  width: 100%;
}

.mainContainer {
  margin-top: 27px; /* Add margin to avoid overlapping with the fixed topnav */
  /* height: calc(100vh - 60px); Adjust height to account for fixed topnav */
  overflow-y: auto; /* Enable scrolling */
}

/* Hide the scrollbar */
.container::-webkit-scrollbar {
  width: 0;
  background: transparent; /* For browsers supporting this property */
}

/* For Firefox */
.container {
  scrollbar-width: none; /* For Firefox */
}

/* Hide the scrollbar */
.mainContainer::-webkit-scrollbar {
  width: 0;
  background: transparent; /* For browsers supporting this property */
}

/* For Firefox */
.mainContainer {
  scrollbar-width: none; /* For Firefox */
}

.topnav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #90eb63;
  padding: .5rem;
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 1000;
}

.logo img {
  height: 4rem;
  width: 4rem;
}

.bars {
  display: none; /* Initially hidden */
  cursor: pointer;
  color: white;
  margin-left: auto;
}

.menu {
  display: flex;
  flex-grow: 1;
  justify-content: flex-end;
  margin-right: 50px;
}

.menu.open {
  display: block; /* Display when open */
}

.link {
  color: black;
  padding: .7rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  font-weight: 700;
}

.link .icon {
  margin-right: 0.5rem;
}

.link_text {
  font-size: 1rem;
  font-family: "myriadBold";
}
.company_text {
  font-size: 2rem;
  font-family: "myriadBold";
  color: black;
  margin-left: 1rem;
  text-decoration: none;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  font-weight: 600;
}

.active {
  background-color: #0d7009;
  border-radius: 5px;
}

.sign_out {
  color: red;
  margin-left: auto;
  cursor: pointer;
}

@media (max-width: 768px) {
  .menu {
      display: none;
      flex-direction: column;
      width: 100%;
      position: absolute;
      top: 5rem;
      left: 0;
      background-color: #333;
  }

  .bars {
      display: block;
  }

  .link {
      justify-content: center;
  }
}

